.modal__close {

  position: absolute;
  right: 2.5rem;
  background: none;
  border: none;
  top: 2rem;
  cursor: pointer;
  fill: $text-black;

  height: 14px;
  width: 14px;

  &:hover,
  &:focus {
    fill: lighten(white, 15%);
  }

  &:active {
    color: darken(white, 25%);
  }
}



