.products {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  margin-top: 12rem;
  color: $text-black;
  margin-bottom: 12rem;



  @media (max-width: $tablet-width) {
    margin-top: 7rem;
    max-width: 768px;
    min-height: 650px;
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    margin-top: 6.5rem;
    min-height: 420px;
  }
}

.products h2 {
  font-weight: bold;
  letter-spacing: 0.03em;
  font-size: 34px;
  line-height: 44px;

  @media (max-width: $tablet-width) {
    padding-left: 2rem;
    line-height: 35px;
  }

@media (max-width: $mobile-width) {
    padding-left: 0;
  }

}

.products a {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
line-height: 21px;
  color: $text-black;
  border-bottom: 1px solid $primary;


}

.slider {
  position: relative;

  @media (max-width: $tablet-width) {
    max-width: 700px;
    margin: 0 auto;

  }

}

.slider__item {
  transition: all 0.3s ease 0s;
  margin-top: 6rem;
  margin-right: 3rem;
  cursor: pointer;

  @media (max-width: $mobile-width) {
    margin-top: 4rem;
  }
}

