.slider__wrapper img {
  width: 100%;

  max-height: 284px;
//  object-fit: cover;
  @media (max-width: $tablet-width) {
    max-width: 324px;
    min-height: 340px;
  }



  @media (max-width: $mobile-width) {
      object-fit: cover;
    max-width: 136px;
    max-height: 130px;
    min-height: inherit;

  }


//  @media (max-width: $tablet-width) {
