//    object-fit: contain;
//  }
}

.slider__item h3 {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 0;

  @media (max-width: $mobile-width) {
    margin-top: 1rem;
  }
}

.slider__item p {
  font-size: 14px;
  line-height: 18px;
  font-family: "Poppins", "Arial", sans-serif;
  margin-top: 0.5rem;
  margin-bottom: 3rem;
}

.slider .slick-arrow {
  position: absolute;
  top: -10%;
  z-index: 1;
  font-size: 0;
  cursor: pointer;
}

.slider .slick-arrow.slick-prev {
  left: 1.5rem;
  background: url(".././img/vector.svg") 0 0 / 100% no-repeat;
  width: 3rem;
  height: 1rem;
  background-color: transparent;
  transform: rotate(180deg);
  outline: none;
  border: none;

  @media (max-width: $tablet-width) {
    left: 0.5rem;
    height: 2rem;
    width: 4rem;
  }
}

.slider .slick-arrow.slick-next {
  right: 1.5rem;
  background: url(".././img/vector.svg") 0 0 / 100% no-repeat;
  width: 3rem;
  height: 1rem;
  stroke: black;
  background-color: transparent;
  outline: none;
  border: none;

  @media (max-width: $tablet-width) {
    right: 0.5rem;
    height: 2rem;
    width: 4rem;
  }



}

.slider .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin-top: 2rem;

  @media (max-width: $tablet-width) {
    margin-top: 1rem;

  }
}


.slider .slick-dots li {
  list-style: none;

}

.slider .slick-dots li.slick-active button {
  color: black;
  opacity: 1;
}

.slider .slick-dots button {
  outline: none;
  border: none;
  background-color: transparent;
  opacity: 0.5;

  @media (max-width: $tablet-width) {

  }
}

.slick-track {
  display: flex;
  align-items: flex-start;
  overflow: hidden;
  padding-left: 0.5rem;
  padding-right: 0.5rem;

  @media (max-width: $tablet-width) {
    padding-left: 2rem;
  padding-right: 2rem;
  }

}

.slick-list {
  overflow: hidden;

}



// catalog.html

.slider--catalog h3 {
  font-weight: 500;
}


.products--catalog {
  display: flex;
  margin-top: 8rem;
  max-width: 1200px;
  padding-right: 1rem;
  padding-left: 1rem;

  @media (max-width: $tablet-width) {
    display: block;
  }
}
.filter-toggle {
  display: none;

  @media (max-width: $tablet-width) {
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 678px;
    background-color: $primary;
    height: 68px;
    color: white;
    font-family: "Poppins", "Arial", sans-serif;
    margin-bottom: 5rem;

  }
}


.slider--catalog {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, min-content);
  grid-row-gap: 0;
  grid-column-gap: 3rem;



  @media (max-width: $mobile-width) {
    display: grid;
    grid-template: repeat(6, min-content) / repeat(2, 1fr);


  }
}

.slider--catalog .slider__item {
  margin-top: 0;
  margin-right: 0;
}

.slider--catalog  img {
  width: 100%;

  max-height: 272px;
//  object-fit: cover;
  @media (max-width: $tablet-width) {
    max-width: 206px;
    max-height: 208px;
  }



  @media (max-width: $mobile-width) {
//    width: 100%;
    max-width: 130px;
    max-height: 136px;

  }
}

.slider--catalog .slider__item p {
  margin-bottom: 3.5rem;
}

.filter {
//  max-width: 270px;
  margin-right: 0;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: 3.5rem;



   @media (max-width: $tablet-width) {
     position: absolute;
     top: 0;
     left: 0;
     background-color: white;
     z-index: 10;
    width: 100%;
    height: 100%;
  }

  @media (max-width: $mobile-width) {
    padding-top: 7.5rem;
  }
}

.filter form {
  @media (max-width: $tablet-width) {
    position: relative;
      max-width: 412px;

  }
}

.filter--closed {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.filter__content {
    border: 1px solid $primary;
  width: 250px;
  margin: 0 auto;
  text-align: left;


@media (max-width: $tablet-width) {
    width: 100%;
//  margin-left: 0;
  border: none;
  }

  @media (max-width: $tablet-width) {
    width: 280px;
  }
}

.filter-item {
  border-bottom: 1px solid $primary;
}

.filter-item__content {
   margin-left: 5rem;
    background-color: white;
    max-height: 0;
  max-width: 52rem;
  text-align: left;
//  padding-bottom: 3rem;
  opacity: 0;
  margin-bottom: 3rem;
  overflow: hidden;
  transition: max-height 0.5s ease-out;

  @media (max-width: $tablet-width) {
    margin: 0;
  }
}



.filter-item__trigger {


font-family: "Poppins", "Arial", sans-serif;
  display: block;
  color: $text-black;
  cursor: pointer;
  padding-top: 3rem;
  padding-left: 4rem;
  padding-bottom: 0;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
  background-color: white;
  position: relative;

  @media (max-width: $tablet-width) {
     padding-left: 0;
    height: 92px;
    padding-top: 0;
  }

  @media (max-width: $mobile-width) {
    height: 72px;
  }
  }

.filter-item__trigger::after {
  position: absolute;
  content: "";
  right: 0;
  background-image: url(".././img/downarrow.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  font-size: 18px;
  float: right;
  margin-right: 4.5rem;
  top: 65%;
//  margin-top: 1rem;

  @media (max-width: $tablet-width) {
       position: absolute;
          right: 40px;
    top: 5rem;
    width: 25px;
  height: 10px;
      margin-right: 0;
  }



}



.filter__label {
    padding-left: 2rem;

  @media (max-width: $tablet-width) {
    padding-left: 5rem;
  }

  @media (max-width: $mobile-width) {
     padding-left: 6.5rem;
  }

}

.filter__input {
  position: absolute;
  appearance: none;

}

// Checked

.filter__input:checked + .checkbox {
  background-image: url(".././img/on.svg");
}

.filter__input:focus + .checkbox {
   box-shadow: 0 0 0 0.2rem $primary;
  opacity: 0.5;
}



.filter-item h2 {
   font-size: 16px;
line-height: 130%;
 font-family: "Poppins", "Arial", sans-serif;
  margin-left: 1rem;
  margin-top: 2.5rem;
  font-weight: 500;
  margin-bottom: 1rem;

  @media (max-width: $tablet-width) {
    font-size: 18px;
    padding-left: 0;
    margin-top: 4rem;
  }

  @media (max-width: $mobile-width) {
    margin-left: 2.5rem;
    margin-top: 1rem;
  }

}

.filter-item ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 2rem;

  @media (max-width: $mobile-width) {
    margin-top: 0;
   margin-bottom: 3rem;
  }

}

.filter-item li {
  margin-bottom: 1rem;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
line-height: 130%;

  @media (max-width: $tablet-width) {
    margin-bottom: 2rem;
  }

}

.checkbox {
  position: absolute;
  width: 16px;
  height: 15px;
  background-image: url(".././img/off.svg");
  margin-left: -2rem;

  @media (max-width: $tablet-width) {
    margin-left: -3rem;
  }

}

.active1::after {
  content: "";
  background-image: url(".././img/arrowup.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  font-size: 18px;
  float: right;
  margin-left: 4px;
  margin-top: 1rem;

   @media (max-width: $tablet-width) {
       position: absolute;
          right: 40px;
    top: 5rem;
    width: 25px;
  height: 10px;
      margin-right: 0;
  }



   @media (max-width: $mobile-width) {
     top: 2rem;

  }
}

.filter__buttons button[type="submit"] {
    font-family: "Poppins", "Arial", sans-serif;
  outline: none;
  border: none;
  width: 250px;
height: 68px;
  background-color: $primary;
  margin-top: 3.5rem;
  margin-bottom: 2.5rem;
  color: white;
  font-size: 16px;
  line-height: 130%;

  @media (max-width: $tablet-width) {
    width: 362px;
  }

  @media (max-width: $mobile-width) {
    width: 230px;
  }
}

.filter__buttons button[type="reset"] {
  font-family: "Poppins", "Arial", sans-serif;
  outline: none;
  border: none;
  width: 250px;
  height: 68px;
  background-color: transparent;
  border: 1px solid $primary;
  color: $text-black;
  font-size: 16px;
  line-height: 130%;

  @media (max-width: $tablet-width) {
    width: 362px;
  }

  @media (max-width: $mobile-width) {
    width: 230px;
  }
}

.slider--catalog ol {
  display: flex;
  list-style: none;
  margin-top: 0;

  @media (max-width: $tablet-width) {

    justify-content: center;
    padding-left: 0;
  }
}

.slider--catalog li {

  margin-right: 1.5rem;
  color: #9B9997;
}

.slider--catalog li:first-child {
  color: black;

}

.slider__pages {
  font-family: "Poppins", "Arial", sans-serif;
  width: 100%;
  display: flex;
  margin: 0 auto;
  text-align: center;
  justify-content: space-between;

  grid-column: 1 / 4;

  @media (max-width: $tablet-width) {
   display: block;
  }


}

.slider__pages a {

  height: 20px;
  border: none;
  font-size: 16px;
line-height: 130%;

  @media (max-width: $tablet-width) {
   display: none;
  }
}

.range-controls {
    position: relative;
    margin-bottom: 15px;
    padding-top: 20px;
    padding-right: 0px;
    padding-left: 0px;
    border-radius: 5px;
}

.range-controls .scale {
    height: 2px;
    width: 9rem;
    background-color: rgba(000, 000, 000, 0.3);

}



.range-controls .toggle {
    position: absolute;
    top: 10px;
    left: 0px;
    width: 19px;
    height: 19px;
    padding: 0;
    border: 8px solid var(--basic-white);
    background-color: $primary;
    border-radius: 50%;
    box-shadow: 0 2px 1px 0 var(box-shadow-toggle);
    cursor: pointer;
}

.range-controls .toggle-min {
    left: 0px;
}

.range-controls .toggle-max {
    left: 100px;
}



.filter__close {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    position: absolute;
    right: 0;
    z-index: 1000;
    background-color: $white;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: black;
    background-image: url(".././img/cross.svg");
    background-repeat: no-repeat;
    background-position: 7px 7px;
  }


}


// card.html


.products--card {
  margin-top: 11rem;
  margin-bottom: 14rem;
  max-width: 1180px;

  @media (max-width: $tablet-width) {
    margin-top: 6rem;
    margin-bottom: 3rem;
  }

  @media (max-width: $mobile-width) {
    margin-top: 4rem;
    margin-bottom: 4.5rem;
  }


}

.products--card h2 {
  font-size: 34px;
line-height: 130%;

  @media (max-width: $tablet-width) {
    margin-bottom: 2rem;
    padding-left: 0;

  }

  @media (max-width: $mobile-width) {
      font-size: 32px;

    margin-top: 4.5rem;

  }


}

.slider--card .slider__item {

  @media (max-width: $tablet-width) {
    margin-top: 5rem;
  }
}

.slider__wrapper--card img {
//  min-width: 270px;
//  min-height: 284px;

  @media (max-width: $tablet-width) {
    min-height: 340px;
    max-width: 324px;

  }
  @media (max-width: $mobile-width) {
    margin: 0 auto;
    object-fit: fill;
//    max-width: 130px;
//    max-height: 136px;
    min-height: 136px;
  }
}

.slider--card {

  @media (max-width: $tablet-width) {
    max-width: 710px;
  }


}

.slider--card .slick-dots  {
  margin-top: 1.7rem;
  height: 12px;
}

.slider--card .slick-arrow.slick-prev {
  left: 0.5rem;

  @media (max-width: $tablet-width) {
    left: 1.5rem;
  }
}

.slider--card .slick-arrow.slick-next {
  right: 0.5rem;

  @media (max-width: $tablet-width) {
    right: 1.5rem;
  }
}

.product__number {
  display: none;
  @media (max-width: $mobile-width) {
    display: block;
    font-family: "Poppins", "Arial", sans-serif;
    font-size: 16px;
line-height: 130%;
  }


}



.slider .slick-dots button {
   @media (max-width: $tablet-width) {
    font-family: "Poppins", "Arial", sans-serif;
    color: #000;
    opacity: 0.5;
    font-size: 16px;
    width: 16px;
    margin-right: .6rem;
  }
}


